\n// private variables\n$nb-theme-process-mode: 'lazy-process' !global;\n$nb-theme-name: null !global;\n$nb-theme: () !global;\n$nb-processed-theme: () !global;\n$nb-theme-export-mode: false !global !default;\n$nb-themes-export: () !global;\n\n@import 'core/mixins';\n@import 'core/functions';\n@import 'core/variants';\n@import 'core/theming/register';\n@import 'core/theming/install';\n@import 'core/theming/get-value';\n","@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap');\n\n// themes - our custom or/and out of the box themes\n@import 'themes';\n// framework component themes (styles tied to theme variables)\n@import '~@nebular/theme/styles/globals';\n@import '~@nebular/auth/styles/all';\n@import '~bootstrap/scss/functions';\n@import '~bootstrap/scss/variables';\n@import '~bootstrap/scss/mixins';\n@import '~bootstrap/scss/grid';\n// loading progress bar theme\n@import './pace.theme';\n@import './layout';\n@import './overrides';\n@import '~@ng-select/ng-select/themes/default.theme.css';\n@import '~@swimlane/ngx-datatable/index.css';\n@import '~@swimlane/ngx-datatable/themes/material.scss';\n@import '~@swimlane/ngx-datatable/themes/dark.scss';\n@import '~@swimlane/ngx-datatable/themes/bootstrap.scss';\n@import '~@swimlane/ngx-datatable/assets/icons.css';\n\n\n// install the framework and custom global styles\n@include nb-install() {\n\n  // framework global styles\n  @include nb-theme-global();\n  @include nb-auth-global();\n\n  @include ngx-layout();\n  // loading progress bar\n  @include ngx-pace-theme();\n\n  @include nb-overrides();\n}\n\n;\n\n.nb-theme-default nb-sidebar {\n  background-color: #1f1f1f;\n}\n\n.nb-theme-default nb-menu .menu-item {\n  //border-bottom: 1px solid #4f5962;\n  border-bottom: none;\n}\n\n.nb-theme-default nb-menu .menu-item .menu-item:first-child {\n  //border-top: 1px solid #4f5962;\n  border-top: none;\n}\n\n.nb-theme-default nb-menu .menu-item a {\n  color: #222b45;\n}\n\n.nb-theme-default nb-menu .menu-item a:hover,\n.nb-theme-default nb-menu .menu-item a.active {\n  background-color: hsla(0, 0%, 100%, 0.1);\n  color: #3183ff;\n}\n\n.nb-theme-default nb-menu .menu-item a:hover .menu-icon,\n.nb-theme-default nb-menu .menu-item a.active .menu-icon {\n  color: #fff;\n}\n\n.nb-theme-default nb-menu .menu-item > .menu-items > .menu-item a.active,\n.nb-theme-default nb-menu .menu-item > .menu-items > .menu-item a:hover {\n  color: #fff;\n  background-color: rgba(255, 255, 255, 0.1);\n}\n\n.nb-theme-default nb-menu .menu-item > .menu-items > .menu-item a.active:hover {\n  color: #fff;\n}\n\n.nb-theme-default nb-layout-header {\n  background-color: #343a40;\n  color: #fff;\n}\n\n.nb-theme-default nb-layout-header nav a {\n  color: #f7f9fc;\n}\n\n.nb-theme-default nb-layout-header nav a:focus,\n.nb-theme-default nb-layout-header nav a:active,\n.nb-theme-default nb-layout-header nav a:hover {\n  color: #ffffff;\n}\n\n.nb-theme-default nb-layout-header nb-action nb-icon {\n  color: #f7f9fc;\n}\n\n.nb-theme-default nb-layout-header nb-user .user-name {\n  text-transform: capitalize;\n  color: #f7f9fc;\n}\n\n.nb-theme-default nb-sidebar .scrollable::-webkit-scrollbar-thumb {\n  background: #141414;\n}\n\n.nb-theme-default nb-sidebar .scrollable::-webkit-scrollbar-track {\n  background: #1f1f1f;\n}\n\n.nb-theme-default nb-sidebar nb-menu .menu-item a {\n  color: #c2c7d0;\n}\n\n* {\n  //font-family: Arial, 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-family: 'Roboto', sans-serif !important;\n}\n\n.fa {\n  font-family: 'Font Awesome 5 Free' !important;\n}\n\n.jexcel_content {\n  max-width: 100%;\n}\n\n.nb-theme-default [nbButton].appearance-ghost:focus {\n  box-shadow: none;\n  -webkit-box-shadow: none;\n  -ms-box-shadow: none;\n}\n\n* {\n  //font-family: Arial, 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-family: 'Roboto', sans-serif !important;\n}\n\n.nb-theme-default .label {\n  color: #030e29;\n}\n\n.padding-custom {\n  padding: 3px !important;\n}\n\n.nb-theme-default nb-card-header {\n  //text-transform: uppercase;\n  //background-color: #bdd5f7;\n  padding-top: 0.5rem;\n  padding-bottom: 0.5rem;\n  color: #4674eb;\n}\n\ninput {\n  border: 1px solid #d2d2d2;\n}\n\n.btn-group-sm {\n  & button:nth-child(1) {\n    border-top-right-radius: unset !important;\n    border-bottom-right-radius: unset !important;\n  }\n\n  & button:nth-child(2) {\n    border-top-left-radius: unset !important;\n    border-bottom-left-radius: unset !important;\n  }\n}\n\n.row-data-show {\n  word-break: break-word;\n}\n\n.dark-backdrop {\n  background: #080707cc;\n}\n\n.datatable-header-inner {\n  white-space: unset !important;\n}\n\n.datatable-header-cell {\n  white-space: unset !important;\n}\n\n.dialog-90 {\n  max-width: 90vw;\n}\n\n.dialog-60 {\n  max-width: 60vw;\n}\n\nng-dropdown-panel.custom-select-template {\n  overflow-x: visible;\n  width: 300px !important;\n  right: 100px !important;\n}\n\nng-dropdown-panel .ng-dropdown-panel .scroll-host {\n  width: 300px;\n  right: 50px;\n}\n\n.custom-select-template {\n  .ng-select-container {\n    min-height: 80px !important;\n  }\n}\n\n.ng-select.tiny .ng-select-container {\n  min-height: 21px;\n}\n\n.ng-select.ng-select-single.tiny .ng-select-container {\n  height: 21px;\n}\n\n.ng-select.small .ng-select-container {\n  min-height: 33.5px;\n}\n\n.ng-select.ng-select-single.small .ng-select-container {\n  height: 33.5px;\n}\n\n.jexcel_container.fit-container {\n  width: 100%;\n\n  .jexcel_content {\n    width: 100%;\n\n    .jexcel {\n      width: 100%;\n\n      .jexcel_selectall, .jexcel_row {\n        width: 50px !important;\n      }\n    }\n  }\n}\n.detailLabel {\n  color: #2d8ac7!important;\n  text-decoration: #2d8ac7 underline !important;\n  cursor: pointer;\n}\n\n\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for license information.\n */\n\n@mixin nb-scrollbars($fg, $bg, $size, $border-radius: $size / 2) {\n  &::-webkit-scrollbar {\n    width: $size;\n    height: $size;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    background: $fg;\n    cursor: pointer;\n    border-radius: $border-radius;\n  }\n\n  &::-webkit-scrollbar-track {\n    background: $bg;\n  }\n\n  // TODO: remove\n  // For Internet Explorer\n  scrollbar-face-color: $fg;\n  scrollbar-track-color: $bg;\n}\n\n@mixin nb-headings($from: 1, $to: 6) {\n  @for $i from $from through $to {\n    h#{$i} {\n      margin: 0;\n    }\n  }\n}\n\n@mixin nb-outline($outline-width, $outline-color, $inset-shadow-length: 0) {\n  $outset-shadow: 0 0 0 $outline-width $outline-color;\n  box-shadow: $outset-shadow;\n\n  @if ($inset-shadow-length != 0) {\n    &:not(:hover):not(:active) {\n      $inset-shadow: inset $inset-shadow-length $outline-color;\n      box-shadow: $outset-shadow, $inset-shadow;\n    }\n  }\n}\n\n@mixin hover-focus-active {\n  &:focus,\n  &:active,\n  &:hover {\n    @content;\n  }\n}\n\n@mixin center-horizontal-absolute {\n  position: absolute;\n  transform: translate(-50%, 0);\n  left: 50%;\n}\n\n@mixin install-thumb() {\n  $thumb-selectors: (\n    '::-webkit-slider-thumb'\n    '::-moz-range-thumb'\n    '::-ms-thumb'\n  );\n\n  @each $selector in $thumb-selectors {\n    &#{$selector} {\n      -webkit-appearance: none;\n      -moz-appearance: none;\n      @content;\n    }\n  }\n}\n\n@mixin install-track() {\n  $thumb-selectors: (\n    '::-webkit-slider-runnable-track'\n    '::-moz-range-track'\n    '::-ms-track'\n  );\n\n  @each $selector in $thumb-selectors {\n    &#{$selector} {\n      -webkit-appearance: none;\n      -moz-appearance: none;\n      @content;\n    }\n  }\n}\n\n@mixin install-placeholder($color, $font-size, $opacity: 1) {\n  $placeholder-selectors: (\n    '::-webkit-input-placeholder'\n    '::-moz-placeholder'\n    ':-moz-placeholder'\n    ':-ms-input-placeholder'\n  );\n\n  &::placeholder {\n    @include placeholder($color, $font-size, $opacity);\n  }\n\n  @each $selector in $placeholder-selectors {\n    &#{$selector} {\n      @include placeholder($color, $font-size, $opacity);\n    }\n\n    &:focus#{$selector} {\n      @include placeholder-focus();\n    }\n  }\n}\n\n@mixin placeholder($color, $font-size, $opacity) {\n  color: $color;\n  font-size: $font-size;\n  opacity: $opacity;\n  transition: opacity 0.3s ease;\n  text-overflow: ellipsis;\n}\n\n@mixin placeholder-focus() {\n  opacity: 0;\n  transition: opacity 0.3s ease;\n}\n\n@mixin nb-component-animation($properties...) {\n  transition-duration: 0.15s;\n  transition-property: $properties;\n  transition-timing-function: ease-in;\n}\n\n@mixin animation($animate...) {\n  $max: length($animate);\n  $animations: '';\n\n  @for $i from 1 through $max {\n    $animations: #{$animations + nth($animate, $i)};\n\n    @if $i < $max {\n      $animations: #{$animations + ', '};\n    }\n  }\n  -webkit-animation: $animations;\n  -moz-animation:    $animations;\n  -o-animation:      $animations;\n  animation:         $animations;\n}\n\n@mixin keyframes($animationName) {\n  @-webkit-keyframes #{$animationName} {\n    @content;\n  }\n  @-moz-keyframes #{$animationName} {\n    @content;\n  }\n  @-o-keyframes #{$animationName} {\n    @content;\n  }\n  @keyframes #{$animationName} {\n    @content;\n  }\n}\n\n/**\n * This mixin generates keyfames.\n * Because of all keyframes can't be scoped,\n * we need to puts unique name in each btn-pulse call.\n */\n@mixin btn-pulse($name, $color) {\n  &.btn-pulse {\n    @include animation(btn-#{$name}-pulse 1.5s infinite);\n  }\n\n  @include keyframes(btn-#{$name}-pulse) {\n    0% {\n      box-shadow: none;\n      opacity: nb-theme(btn-disabled-opacity);\n    }\n    50% {\n      box-shadow: 0 0 1rem 0 $color;\n      opacity: 0.8;\n    }\n    100% {\n      box-shadow: none;\n      opacity: nb-theme(btn-disabled-opacity);\n    }\n  }\n}\n\n/*\n\nAccording to the specification (https://www.w3.org/TR/css-scoping-1/#host-selector)\n:host and :host-context are pseudo-classes. So we assume they could be combined,\nlike other pseudo-classes, even same ones.\nFor example: ':nth-of-type(2n):nth-of-type(even)'.\n\nIdeal solution would be to prepend any selector with :host-context([dir=rtl]).\nThen nebular components will behave as an html element and respond to [dir] attribute on any level,\nso direction could be overridden on any component level.\n\nImplementation code:\n\n@mixin nb-rtl() {\n  // add # to scss interpolation statement.\n  // it works in comments and we can't use it here\n  @at-root {selector-append(':host-context([dir=rtl])', &)} {\n    @content;\n  }\n}\n\nAnd when we call it somewhere:\n\n:host {\n  .some-class {\n    @include nb-rtl() {\n      ...\n    }\n  }\n}\n:host-context(...) {\n  .some-class {\n    @include nb-rtl() {\n      ...\n    }\n  }\n}\n\nResult will look like:\n\n:host-context([dir=rtl]):host .some-class {\n  ...\n}\n:host-context([dir=rtl]):host-context(...) .some-class {\n  ...\n}\n\n*\n  Side note:\n  :host-context():host selector are valid. https://lists.w3.org/Archives/Public/www-style/2015Feb/0305.html\n\n  :host-context([dir=rtl]):host-context(...) should match any permutation,\n  so order is not important.\n*\n\n\nCurrently, there're two problems with this approach:\n\nFirst, is that we can't combine :host, :host-context. Angular bugs #14349, #19199.\nFor the moment of writing, the only possible way is:\n:host {\n  :host-context(...) {\n    ...\n  }\n}\nIt doesn't work for us because mixin could be called somewhere deeper, like:\n:host {\n  p {\n    @include nb-rtl() { ... }\n  }\n}\nWe are not able to go up to :host level to place content passed to mixin.\n\nThe second problem is that we only can be sure that we appending :host-context([dir=rtl]) to another\n:host/:host-context pseudo-class when called in theme files (*.theme.scss).\n  *\n    Side note:\n    Currently, nb-install-component uses another approach where :host prepended with the theme name\n    (https://github.com/angular/angular/blob/5b96078624b0a4760f2dbcf6fdf0bd62791be5bb/packages/compiler/src/shadow_css.ts#L441),\n    but it was made to be able to use current realization of rtl and it can be rewritten back to\n    :host-context($theme) once we will be able to use multiple shadow selectors.\n  *\nBut when it's called in *.component.scss we can't be sure, that selector starts with :host/:host-context,\nbecause angular allows omitting pseudo-classes if we don't need to style :host component itself.\nWe can break such selectors, by just appending :host-context([dir=rtl]) to them.\n  ***\n    Possible solution\n    check if we in theme by some theme variables and if so append, otherwise nest like\n    @at-root :host-context([dir=rtl]) {\n      // add # to scss interpolation statement.\n      // it works in comments and we can't use it here\n      {&} {\n        @content;\n      }\n    }\n    What if :host specified? Can we add space in :host-context(...) :host?\n    Or maybe add :host selector anyway? If multiple :host selectors are allowed\n  ***\n\n\nProblems with the current approach.\n\n1. Direction can be applied only on document level, because mixin prepends theme class,\nwhich placed on the body.\n2. *.component.scss styles should be in :host selector. Otherwise angular will add host\nattribute to [dir=rtl] attribute as well.\n\n\nGeneral problems.\n\nLtr is default document direction, but for proper work of nb-ltr (means ltr only),\n[dir=ltr] should be specified at least somewhere. ':not([dir=rtl]' not applicable here,\nbecause it's satisfy any parent, that don't have [dir=rtl] attribute.\nPrevious approach was to use single rtl mixin and reset ltr properties to initial value.\nBut sometimes it's hard to find, what the previous value should be. And such mixin call looks too verbose.\n*/\n\n@mixin _prepend-with-selector($selector, $prop: null, $value: null) {\n  #{$selector} & {\n    @if $prop != null {\n      #{$prop}: $value;\n    }\n\n    @content;\n  }\n}\n\n@mixin nb-ltr($prop: null, $value: null) {\n  @include _prepend-with-selector('[dir=ltr]', $prop, $value) {\n    @content;\n  }\n}\n\n@mixin nb-rtl($prop: null, $value: null) {\n  @include _prepend-with-selector('[dir=rtl]', $prop, $value) {\n    @content;\n  };\n}\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for license information.\n */\n\n/// Slightly lighten a color\n/// @access public\n/// @param {Color} $color - color to tint\n/// @param {Number} $percentage - percentage of `$color` in returned color\n/// @return {Color}\n@function tint($color, $percentage) {\n  @return mix(white, $color, $percentage);\n}\n\n/// Slightly darken a color\n/// @access public\n/// @param {Color} $color - color to shade\n/// @param {Number} $percentage - percentage of `$color` in returned color\n/// @return {Color}\n@function shade($color, $percentage) {\n  @return mix(black, $color, $percentage);\n}\n\n@function map-set($map, $key, $value: null) {\n  $new: ($key: $value);\n  @return map-merge($map, $new);\n}\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for license information.\n */\n\n@function nb-get-statuses() {\n  @return 'basic', 'primary', 'success', 'warning', 'danger', 'info', 'control';\n}\n\n@function nb-get-sizes() {\n  @return 'tiny', 'small', 'medium', 'large', 'giant';\n}\n\n@function nb-get-shapes() {\n  @return 'rectangle', 'semi-round', 'round';\n}\n\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for license information.\n */\n\n$eva-mapping: (\n\n  /* Components mappings - maps theme variables onto component variables */\n\n  link-text-color: text-primary-color,\n  link-text-decoration: underline,\n  link-text-focus-color: text-primary-focus-color,\n  link-text-hover-color: text-primary-hover-color,\n\n  card-background-color: back